body {
  font: 14px "Century Gothic", Futura, sans-serif;
  margin: 20px;
}

ol, ul {
  padding-left: 30px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.status {
  margin-bottom: 10px;
}

.square {
  background: #fff;
  border: 1px solid #999;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 34px;
  height: 34px;
  margin-right: -1px;
  margin-top: -1px;
  padding: 0;
  text-align: center;
  width: 34px;
}

.square:focus {
  outline: none;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  flex-direction: row;
}

.game-info {
  margin-left: 20px;
}

.moves-btn {
  background: none;
  border: none;
}

.moves-btn:hover {
  text-decoration: underline;
}

.bold {
  font-weight: bold;
}

.reverse-list {
  display: flex;
  flex-direction: column-reverse;
}

.highlight {
  background-color: yellow;
}

.display {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.margin-bottom {
  margin-bottom: 2rem;
}

@media (min-width: 700px) {
  .display {
    flex-direction: row;
  }
}
